<script>
  import {fetchList, updateStatus, deleteFlash, createFlash, updateFlash} from '@/api/flash';
  import {getProjectList, createProject, updataProject, delProject} from '@/api/api';
  import {formatDate} from '@/utils/date';
  import { Message, MessageBox } from 'element-ui';

  const defaultListQuery = {
    pageNum: 1,
    pageSize: 10,
    name: '',
    sn: ''
  };
  const defaultFlashPromotion = {
    id: null,
    name: null,
    sn: null,
    open_time: null,
    close_time: null,
    desc: null,
    members: null
  };
  export default {
    name: 'flashPromotionList',
    data() {
      return {
        listQuery: Object.assign({}, defaultListQuery),
        list: null,
        total: null,
        searchs: {}, //æç»ç¡®å®çæç´¢ä¿¡æ¯ï¼éè¦å¤çè¿
        listLoading: false,
        dialogVisible: false,
        flashPromotion: Object.assign({}, defaultFlashPromotion),
        isEdit: false,
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() < Date.now();
          }
          }
    }},
    created() {
        this.listLoading = true;
        getProjectList(this.listQuery).then(response =>{
            this.listLoading = false;
            this.list = response.data;
            this.total = response.count;
        })
    },
    filters: {
      formatActiveStatus(row) {
        let nowTime = new Date().getTime();
        if (nowTime >= row.startDate && nowTime <= row.endDate) {
          return 'æ´»å¨è¿è¡ä¸­';
        } else if (nowTime > row.endDate) {
          return 'æ´»å¨å·²ç»æ';
        } else {
          return 'æ´»å¨æªå¼å§';
        }
      },
      formatDate(time) {
        if (time == null || time === '') {
          return 'N/A';
        }
        let date = new Date(time);
        return formatDate(date, 'yyyy-MM-dd')
      }
    },
    methods: {
        getData(){
            this.listLoading= true;
            getProjectList({pageNum: this.listQuery.pageNum, pageSize: this.listQuery.pageSize, name: this.listQuery.name, 
            sn: this.listQuery.sn }).then(response =>{
                this.listLoading = false;
                this.list = response.data;
                this.total = response.count;
        })
        },
      
      // éç½®
      handleResetSearch() {
        this.listQuery = Object.assign({}, defaultListQuery);
      },

      // æç´¢
      search () {
        this.listQuery.pageNum = 1;
        this.searchs = this.listQuery;
        console.log(this.listQuery)
        this.getData();
        },
        // åé¡µç¸å³æä½
      handleSizeChange(val) {
        this.listQuery.pageNum = 1;
        this.listQuery.pageSize = val;
        this.getData();
      },
      handleCurrentChange(val) {
        this.listQuery.pageNum = val;
        this.getData();
      },

      handleAdd() {
        this.dialogVisible = true;
        this.isEdit = false;
        this.flashPromotion = Object.assign({},defaultFlashPromotion);
      },

      handleStatusChange(index, row) {
        this.$confirm('æ¯å¦è¦ä¿®æ¹è¯¥ç¶æ?', 'æç¤º', {
          confirmButtonText: 'ç¡®å®',
          cancelButtonText: 'åæ¶',
          type: 'warning'
        }).then(() => {
          updateStatus(row.id, {status: row.status}).then(response => {
            this.$message({
              type: 'success',
              message: 'ä¿®æ¹æå!'
            });
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: 'åæ¶ä¿®æ¹'
          });
          this.getData();
        });
      },
      handleDelete(index, row) {
        this.$confirm('æ¯å¦è¦å é¤è¯¥é¡¹ç®?', 'æç¤º', {
          confirmButtonText: 'ç¡®å®',
          cancelButtonText: 'åæ¶',
          type: 'warning'
        }).then(() => {
          delProject(row.id).then(response => {
            this.$message({
              type: 'success',
              message: 'å é¤æå!'
            });
            this.getData();
          });
        });
      },
      handleUpdate(index, row) {
        this.dialogVisible = true;
        this.isEdit = true;
        this.flashPromotion = Object.assign({},row);
      },
      handleDialogConfirm() {
        this.$confirm('æ¯å¦è¦ç¡®è®¤?', 'æç¤º', {
          confirmButtonText: 'ç¡®å®',
          cancelButtonText: 'åæ¶',
          type: 'warning'
        }).then(() => {
          if (this.isEdit) {
            updataProject(this.flashPromotion.id,this.flashPromotion).then(response => {
              if ('id' in response){
                this.$message({
                message: 'ä¿®æ¹æåï¼',
                type: 'success'
              });
              this.dialogVisible =false;
              this.getData();
              }else{
                Message({
                message: response.name[0],
                type: 'error',
                duration: 3 * 1000
                })
              }
            })
          } else {
            createProject(this.flashPromotion).then(response => {
              this.$message({
                message: 'æ·»å æåï¼',
                type: 'success'
              });
              this.dialogVisible =false;
              this.getData();
            })
          }
        })
      },
      handleSelectSession(index,row){
        this.$router.push({path:'/sms/selectSession',query:{flashPromotionId:row.id}})
      },
      getList() {
        this.listLoading = true;
        fetchList(this.listQuery).then(response => {
          this.listLoading = false;
          this.list = response.data.list;
          this.total = response.data.total;
        });
      }
    }
  }
</script>
